.rcpo-sortable #the-list {
    > tr {
        transition: box-shadow 250ms ease-in-out;

        > :first-child {
            position: relative;

            .rcpo-handler {
                opacity: 0;
                background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAAGCAYAAAACEPQxAAAAEklEQVQYV2O4//DJfwYEQOUCAJEzB0enKK/kAAAAAElFTkSuQmCC)
                    repeat;
                width: 10px;
                position: absolute;
                cursor: move;
                top: 5px;
                left: -12px;
                bottom: 5px;
                border: 1px solid #dfe1e4;
                border-radius: 10px 0 0 10px;

                &:hover {
                    opacity: 1;
                    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAAGCAYAAAACEPQxAAAAEklEQVQYV2M4fvz4fwYEQOUCAISWBqkEOFRDAAAAAElFTkSuQmCC)
                        repeat;
                }
            }
        }

        &:hover .rcpo-handler,
        &.rcpo-force-handler .rcpo-handler {
            opacity: 1;
        }

        &.updating-sequence .rcpo-handler .spinner {
            visibility: visible;
            top: 26px;
            right: -30px;
            position: absolute;
            margin: 0;
            opacity: 1;
        }
    }

    .ui-sortable-helper {
        background-color: #f9f9f9;
        border-top: 1px solid #dfdfdf;
        border-bottom: 1px solid #dfdfdf;
        box-shadow: 0 -5px 5px -4px #0000001a, 0 5px 5px -3px #0000001a;

        .rcpo-handler {
            opacity: 1 !important;
        }
    }
}

.rcpo-handler-pointer {
    .wp-pointer-content {
        margin-left: -25px !important;
    }

    .wp-pointer-arrow {
        left: -9px !important;
    }

    .wp-pointer-content,
    .wp-pointer-arrow {
        margin-top: 5px !important;
    }

    .rcpo-pointer-buttons .button {
        margin-left: 5px;
    }
}
